.footer {
  width: 320px;
  background-color: @basic_grey;
  padding: 40px 20px 40px 20px;

  @media (min-width: @tablet-width) {
    width: 768px;
    padding: 57px 30px;
    display: flex;
    align-items: center;
  }

  @media (min-width: @desktop-width) {
    width: 1440px;
    padding: 66px 110px;
  }

  &__logo-text {
    display: flex;
    justify-content: center;
    padding-bottom: 20.5px;
    border-bottom: 1px solid @line-medium_light-gray;

    @media (min-width: @tablet-width) {
      display: block;
      padding: 0;
      border: none;
      margin-right: 160px;
    }

    @media (min-width: @desktop-width) {
      margin-right: 416px;
    }
  }

  &__social-link {
    width: 280px;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 74px;
    padding-right: 74px;
    border-bottom: 1px solid @line-medium_light-gray;

    @media (min-width: @tablet-width) {
      width: 132px;
      padding: 0;
      border: none;
    }
  }

  &__htmlacademy {
    padding-top: 20px;

    @media (min-width: @tablet-width) {
      padding: 0;
      margin-left: auto;
    }
  }
}
